@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
.style_logo__JyTdN{border:2px solid #000000;border-radius:3px;padding:0 5px}.style_logo__JyTdN h2{color:black;margin-bottom:0}

header{position:fixed;top:0;left:0;padding:10px 0;width:100%;display:flex;background-color:white}header button{font-size:1.61em}header .logo img{height:50px}header .logo:hover{text-decoration:none}header .top{display:flex;justify-content:space-between;align-items:center}header .open{-webkit-clip-path:circle(100vh) !important;clip-path:circle(100vh) !important}header .nav{position:fixed;top:60px;left:0;height:100vh;width:100vw;display:flex;flex-direction:column;justify-content:space-around;align-items:center;background-color:#f5f5f7;-webkit-clip-path:circle(0);clip-path:circle(0);transition:all 1s cubic-bezier(0.18, 0.89, 0.32, 1.28)}header .nav li{font-size:2em}

.input-group-prepend .btn{border-top-left-radius:50rem !important;border-bottom-left-radius:50rem !important}.input-group-append .btn{border-top-right-radius:50rem !important;border-bottom-right-radius:50rem !important}.product-color{text-align:center}.product-color img{height:50vh;transform:rotate(30deg)}.product-color h6{text-transform:uppercase}.product-color .choice-of-color{display:flex;justify-content:center;padding:0;margin:0}.product-color .choice-of-color li{list-style:none;padding:20px;margin:0 5px;border-radius:2em;cursor:pointer;transition:all .6s ease}.product-color .choice-of-color li:hover,.product-color .choice-of-color li.active{box-shadow:1px 1px 5px #c0c0c0;transform:scale(1.2)}.product-color .choice-of-color li:nth-of-type(1){background-color:#262626}.product-color .choice-of-color li:nth-of-type(2){background-color:#5f6161}.product-color .choice-of-color li:nth-of-type(3){background-color:#4ce1ce}.product-color .choice-of-color li:nth-of-type(4){background-color:#dc1721}.product-color .choice-of-color li:nth-of-type(5){background-color:#daba87}@media screen and (min-width: 1000px){.product-color img{height:60vh}}

.step-three{padding:20px 0}.step-three button{margin-top:20px}.step-three .form-check{padding:10px 0 10px 20px}.step-three label,.step-three input{cursor:pointer;margin-top:0}.step-three label{display:flex;justify-content:space-between;align-items:center}.step-three .payment-icons img{margin-right:15px}.step-three .payment-icons img:nth-of-type(1){width:60.8px}.step-three .payment-icons img:nth-of-type(2){width:48px}.step-three .payment-icons img:nth-of-type(3){width:38.4px}.step-three .payment-icons img:nth-of-type(4){width:56px}.step-three .payment-icons #webmoney{width:110px}@media screen and (min-width: 768px){.step-three .payment-icons img:nth-of-type(1){width:64.6px}.step-three .payment-icons img:nth-of-type(2){width:51px}.step-three .payment-icons img:nth-of-type(3){width:40.8px}.step-three .payment-icons img:nth-of-type(4){width:59.5px}}@media screen and (min-width: 1024px){.step-three .payment-icons img:nth-of-type(1){width:76px}.step-three .payment-icons img:nth-of-type(2){width:60px}.step-three .payment-icons img:nth-of-type(3){width:48px}.step-three .payment-icons img:nth-of-type(4){width:70px}.step-three .payment-icons #webmoney{width:130px}}

form{max-width:480px;text-align:center}form .checked-image{padding:20px 0}

footer{color:#ffffff;background-color:#151515;text-align:center}footer .col-12:nth-of-type(3){display:flex;justify-content:center;align-items:center}footer a{color:rgba(255,255,255,0.8);text-decoration:underline}footer .payments i{font-size:2rem !important}footer .row div:nth-of-type(2){padding:20px 0}footer .list-group{margin:0;justify-content:center}footer .list-group-item{padding:1rem;background-color:transparent;border:none}footer .list-group-item img{width:30px}footer .list-group-item i{color:rgba(255,255,255,0.8);font-size:1.25rem}footer .list-group-item i:hover{color:#fff}@media screen and (min-width: 968px){.col-12{padding-bottom:0}}

.loader-fullscreen{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}

input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}::-webkit-scrollbar{height:8px;width:8px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#d2d2d2}::-webkit-scrollbar-thumb:hover{background:#b8b8b8}button:focus,select:focus,input:focus,textarea:focus{box-shadow:none !important;outline:none !important}html,body{height:100%}body{font-family:'Source Sans Pro', sans-serif;font-size:100%;color:#000000;background-color:white}a{color:#1e8cde}a:hover{color:#1e8cde !important}.row{margin:0}section{padding:60px 0 0}header{z-index:999}#tastes{background:#f5f5f7;background:linear-gradient(0deg, #f5f5f7 0%, #fff 100%)}#tastes .taste-list .taste-item{display:inline-block;vertical-align:top;text-align:center;width:120px;padding:0 15px 10px}#tastes .taste-list .taste-item span{font-size:1rem}#tastes .taste-list .taste-item img{width:100%}#map,#warranty{background-color:#f5f5f7}#map{padding-bottom:50px}@media screen and (min-width: 960px){#tastes .taste-list .taste-item{width:160px}#tastes .taste-list .taste-item span{font-size:1.4rem}section{padding:0;min-height:auto !important}section .section-header{padding:80px 0 100px}.w-lg-60{max-width:60% !important}.w-lg-80{max-width:80% !important}}.btn-outline-danger{color:#dc3545d4 !important;border-color:#dc3545d4 !important}.btn-outline-danger:hover,.btn-outline-danger:focus{color:#ffffff !important;background-color:#dc3545d4 !important;border-color:#dc3545d4 !important}.btn.action{font-size:1.75em !important}@media screen and (min-width: 768px){.btn.action{font-size:1.25em !important}}

